@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background-color: #000000;
}

.container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 3rem;
}

video:focus {
  outline: none !important;
}

video::-webkit-media-controls-panel{
  background: -webkit-gradient(linear, left top, right top, from(rgba(184,183,200,1)), color-stop(0%, rgba(153,153,158,1)), color-stop(65%, rgba(213,213,213,1)));
  background: linear-gradient(90deg, rgba(184,183,200,1) 0%, rgba(153,153,158,1) 0%, rgba(213,213,213,1) 65%);
}
